.BuildControls{
    width: 100%;
    /* background: #cf8f8e; */
    background: #F7BC70;
    display: flex;
    flex-flow: column;
    align-items: center;
    box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}

.Price{
    font-weight: bold;
    font-size: 18px;
}

.OrderButton{
    background: #dad735;
    outline: none;
    cursor: pointer;
    border: 1px solid #966909;
    color: #966909;
    font-family: inherit;
    font-size: 0.8rem;
    padding: 10px 15px;
    box-shadow: 2px 2px 2px #966909;
}

.OrderButton:hover, .OrderButton:active{
    background: #a0db41;
    border: 1px solid #966909;
    color: #966909;
}

.OrderButton:disabled{
    background: #c7c6c6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888;
}

.OrderButton:not(:disabled){
    animation: enable 0.3s linear;
}

@keyframes enable {
    0%{
        transform: scale(1);
    }

    60%{
        transform: scale(1.1);
    }

    100%{
        transform: scale(1);
    }
}