.Toolbar{
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: #703B09;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0rem 1.2rem;
    box-sizing: border-box;
    z-index: 90;
    color: white;
    font-size: 1rem;
    font-weight: bold;
}

@media (max-width: 500px){
    .HideOnMobile{
        display: none;
    }
}